import { useState, useEffect } from "react";
import "./App.css";
import Layout from "./Components/Layout";
import Router from "./core/router";
import Tour from "reactour";
import { isMobile, tourSteps } from "./util/util";
import { BrowserRouter } from "react-router-dom";

const mobile = isMobile();
const steps = tourSteps.filter((step) => {
  if (!(mobile && step.showOnMobile === false)) {
    return true;
  }
});

function App() {
  const [isTourOpen, setIsTourOpen] = useState(false);
  const [nextStep, setNextStep] = useState(undefined);

  const tourClosed = (e) => {
    window.localStorage.setItem("tourDone", true);
    setIsTourOpen(false);
  };

  useEffect(() => {
    const isTourDone = window.localStorage.getItem("tourDone");
    setIsTourOpen(isTourDone ? false : true);
  }, []);
  return (
    <BrowserRouter>
      <Layout>
        <Router />
          <Tour
            steps={steps}
            isOpen={isTourOpen}
            onRequestClose={tourClosed}
            rounded={5}
            lastStepNextButton={<div className="close-btn">Close</div>}
            accentColor="var(--green)"
            showNumber={false}
            className="modal"
            closeWithMask={false}
            startAt={nextStep}
          />
      </Layout>
    </BrowserRouter>
  );
}

export default App;
