import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import {
  IconAppStore,
  IconBookmark,
  IconCodepen,
  IconExternal,
  IconFolder,
  IconFork,
  IconGitHub,
  IconInstagram,
  IconLinkedin,
  IconLoader,
  IconLogo,
  IconPlayStore,
  IconStar,
  IconStackOverflow,
  IconMedium,
  IconLeetCode,
  IconApple,
  IconMicrosoft,
  IconAdobe,
  IconCapital,
  IconAmazon,
  IconFacebook,
  IconGoldman,
  IconGoogle,
  IconAsana,
  IconLyft,
  IconUber,
  IconOracle,
  IconBloomberg,
  IconQuora,
  IconIntuit,
  IconSnapchat,
  IconPinterest,
  IconTwitter,
  IconQualtrics,
  IconLinkedIn,
  IconAirbnb,
  IconJPMorgan,
  IconBlackRock,
  IconAkuna,
  IconSalesforce,
  IconCitadel,
  IconPalantir,
  IconAtlassian,
  IconWalmart,
  IconSAP,
  IconIdea,
  IconHome
} from "../icons";

const Icon = ({ name }) => {
  const [imageIcons, setImageIcons] = useState({});
  useEffect(() => {
    if (name.includes("svg")) {
      import(`../../Assets/images/icons/${name}`).then((image) => {
        // console.log(image); // this may be object with image inside...
        const newImages = { ...imageIcons };
        newImages[name] = image.default;
        setImageIcons(newImages);
      });
    }
    return () => {
      setImageIcons({});
    };
  }, [name]);
  switch (name) {
    case "AppStore":
      return <IconAppStore />;
    case "Bookmark":
      return <IconBookmark />;
    case "Codepen":
      return <IconCodepen />;
    case "External":
      return <IconExternal />;
    case "Folder":
      return <IconFolder />;
    case "Fork":
      return <IconFork />;
    case "GitHub":
      return <IconGitHub />;
    case "Instagram":
      return <IconInstagram />;
    case "Linkedin":
      return <IconLinkedin />;
    case "Loader":
      return <IconLoader />;
    case "Logo":
      return <IconLogo />;
    case "PlayStore":
      return <IconPlayStore />;
    case "Star":
      return <IconStar />;
    case "Twitter":
      return <IconTwitter />;
    case "Stackoverflow":
      return <IconStackOverflow />;
    case "Medium":
      return <IconMedium />;
    case "LeetCode":
      return <IconLeetCode />;
    case "Apple":
      return <IconApple />;
    case "Apple":
      return <IconApple />;
    case "Microsoft":
      return <IconMicrosoft />;
    case "Adobe":
      return <IconAdobe />;
    case "Capital One":
      return <IconCapital />;
    case "Amazon":
      return <IconAmazon />;
    case "Facebook":
      return <IconFacebook />;
    case "Goldman Sachs":
      return <IconGoldman />;
    case "Google":
      return <IconGoogle />;
    case "Asana":
      return <IconAsana />;
    case "Lyft":
      return <IconLyft />;
    case "Uber":
      return <IconUber />;
    case "Oracle":
      return <IconOracle />;
    case "Bloomberg":
      return <IconBloomberg />;
    case "Quora":
      return <IconQuora />;
    case "Intuit":
      return <IconIntuit />;
    case "Snapchat":
      return <IconSnapchat />;
    case "Pinterest":
      return <IconPinterest />;
    case "Twitter":
      return <IconTwitter />;
    case "Qualtrics":
      return <IconQualtrics />;
    case "LinkedIn":
      return <IconLinkedIn />;
    case "Airbnb":
      return <IconAirbnb />;
    case "JPMorgan":
      return <IconJPMorgan />;
    case "BlackRock":
      return <IconBlackRock />;
    case "Akuna Capital":
      return <IconAkuna />;
    case "Salesforce":
      return <IconSalesforce />;
    case "Citadel":
      return <IconCitadel />;
    case "Palantir":
      return <IconPalantir />;
    case "Atlassian":
      return <IconAtlassian />;
    case "Walmart":
      return <IconWalmart />;
    case "SAP":
      return <IconSAP />;
    case "IDEA":
      return <IconIdea />;
    case "HOME":
        return <IconHome />
    default:
      // return <IconExternal />;
      return imageIcons[name] ? (
        <img
          src={imageIcons[name]}
          alt={name}
          height="20px"
          width="20px"
          data-tip={name}
        />
      ) : (
        ""
      );
    // return { imageIcons[name]? <img src={imageIcons[name]} alt={name} data-tip={name} /> };
  }
};

Icon.propTypes = {
  name: PropTypes.string.isRequired,
};

export default Icon;
