import React, { useState, useEffect, useRef } from "react";
// import { CSSTransition, TransitionGroup } from 'react-transition-group';
import styled from "styled-components";
// import { srConfig } from '@config';
// import sr from '@utils/sr';
// import { Icon } from '@components/icons';
// import { usePrefersReducedMotion } from '@hooks';
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import { PieChart } from "react-minimal-pie-chart";
import Table from "../Table/Table";
import ReactTooltip from "react-tooltip";
import ChangingProgressProvider from "../shared/progressProvider";
import Switch from "react-switch";

const StyledProjectsSection = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    font-size: clamp(24px, 5vw, 32px);
  }

  .archive-link {
    font-family: var(--font-mono);
    font-size: var(--fz-sm);
    &:after {
      bottom: 0.1em;
    }
  }

  .projects-grid {
    ${({ theme }) => theme.mixins.resetList};
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(1000px, 1fr));
    grid-gap: 15px;
    position: relative;
    margin-top: 50px;

    @media (max-width: 1080px) {
      grid-template-columns: repeat(auto-fill, minmax(1fr));
    }
  }

  .more-button {
    ${({ theme }) => theme.button};
    margin: 80px auto 0;
  }
`;

const StyledProject = styled.div`
  margin: 20px;
  width: 70%;
  position: relative;
  cursor: default;
  /* justify-content:center; */
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  list-style-type: none;
  @media (prefers-reduced-motion: no-preference) {
    &:hover,
    &:focus-within {
      .project-inner {
        /* transform: translateY(-7px); */
      }
    }
  }
  @media (max-width: 768px) {
    width: 95%;
    margin: 10px;
  }
  .hide-on-mobile {
    @media (max-width: 768px) {
      display: none;
    }
  }
  a {
    position: relative;
    z-index: 1;
  }
  .chart-helper {
    text-align: center;
    margin-top: 10px;
  }

  .project-inner {
    ${({ theme }) => theme.mixins.boxShadow};
    ${({ theme }) => theme.mixins.flexBetween};
    flex-direction: row;
    align-items: center;
    position: relative;
    height: 100%;
    padding: 2rem 1.75rem;
    border-radius: 4px;
    background-color: #112240;
    transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
    width: 100%;
    justify-content: space-around;
    cursor: pointer;
  }

  .project-top {
    ${({ theme }) => theme.mixins.flexBetween};
    margin-bottom: 35px;

    .folder {
      color: var(--green);
      svg {
        width: 40px;
        height: 40px;
      }
    }

    .project-links {
      display: flex;
      align-items: center;
      margin-right: -10px;
      color: #a8b2d1;

      a {
        ${({ theme }) => theme.mixins.flexCenter};
        padding: 5px 7px;

        &.external {
          svg {
            width: 22px;
            height: 22px;
            margin-top: -4px;
          }
        }

        svg {
          width: 20px;
          height: 20px;
        }
      }
    }
  }

  .project-title {
    margin: 0 0 10px;
    color: #ccd6f6;
    font-size: 22px;

    a {
      position: static;

      &:before {
        content: "";
        display: block;
        position: absolute;
        z-index: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
    }
  }
  .question-section {
    background: #192946;
    /* height: 200px; */
    /* border-radius: 4px; */
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 20px;
    z-index: -1;
    /* margin-top: -100px; */
    -webkit-animation: slide-down 3s ease-out;
    -moz-animation: slide-down 3s ease-out;
    animation: slide-down 0.5s ease-out;
  }
  .card-label{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.3em;
    margin-left: 1em;
  }
  @-webkit-keyframes slide-down {
    0% {
      opacity: 0;
      -webkit-transform: translateY(-100%);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(0);
    }
  }
  @-moz-keyframes slide-down {
    0% {
      opacity: 0;
      -moz-transform: translateY(-100%);
    }
    100% {
      opacity: 1;
      -moz-transform: translateY(0);
    }
  }
  @keyframes slide-down {
    0% {
      opacity: 0;
      -moz-transform: translateY(-100%);
    }
    100% {
      opacity: 1;
      -moz-transform: translateY(0);
    }
  }
  .project-description {
    color: #a8b2d1;
    font-size: 17px;

    a {
      ${({ theme }) => theme.mixins.inlineLink};
    }
  }

  .project-tech-list {
    display: flex;
    align-items: flex-end;
    flex-grow: 1;
    flex-wrap: wrap;
    padding: 0;
    margin: 20px 0 0 0;
    list-style: none;

    li {
      font-size: 12px;
      line-height: 1.75;

      &:not(:last-of-type) {
        margin-right: 15px;
      }
    }
  }
`;

const Card = (props) => {
  const [showQuestions, setShowQuestions] = useState(false);
  const { questionList, meta } = props;
  const [questionInfo, setQuestionInfo] = useState({});
  const [solveCount, setSolveCount] = useState(0);
  const [questionsList, setQuestionsList] = useState([]);
  const [hideSolved, setHideSolved] = useState(false);
  const [viewAbleQuestions, setViewAbleQuestions] = useState(false);
  useEffect(() => {}, []);
  useEffect(() => {
    const hideAnswered = window.localStorage.getItem("hide" + meta.category);
    console.log("answered : ", hideAnswered);
    if (hideAnswered === null) {
      window.localStorage.setItem("hide" + meta.category, "false");
      setHideSolved(false);
    }
    const questionCheckedList =
      window.localStorage.getItem("iut2jkqwpxww") || "{}"; //btoa(JSON.stringify({}));
    console.log("********", questionCheckedList);
    // console.log(atob(questionCheckedList));
    const revisedCheckList = [];
    const decidedCheckList = JSON.parse(questionCheckedList); //JSON.parse(atob(questionCheckedList));
    const tempList = [...questionList];
    const tempQuestionInfo = {};
    let tempSolveCount = 0;
    console.log();
    questionList.forEach((element) => {
      const item = tempQuestionInfo[element.difficulty];
      if (item) {
        tempQuestionInfo[element.difficulty] = item + 1;
      } else {
        tempQuestionInfo[element.difficulty] = 1;
      }
    });
    // console.log("Questions : ", questionInfo);
    const viewAbleQuestions = [];
    questionList.forEach((project, i) => {
      if (!decidedCheckList[project.id]) {
        decidedCheckList[project.id] = { checked: false };
        tempList[i].checked = false;
      } else {
        // revisedCheckList.push({ id: project.id, checked: true });
        const checked = decidedCheckList[project.id].checked;
        tempList[i].checked = checked;
        if (checked) tempSolveCount++;
      }
      if (hideAnswered == "true") {
        if (!tempList[i].checked) viewAbleQuestions.push(tempList[i]);
      } else {
        viewAbleQuestions.push(tempList[i]);
      }
    });
    // console.log("******** reviesd list", decidedCheckList);
    setViewAbleQuestions(viewAbleQuestions);
    setHideSolved(hideAnswered == "true" ? true : false);
    setQuestionsList(tempList);
    setSolveCount(tempSolveCount);
    setQuestionInfo(tempQuestionInfo);
    window.localStorage.setItem(
      "iut2jkqwpxww",
      JSON.stringify(decidedCheckList)
      //btoa(JSON.stringify(revisedCheckList))
    );
    // return () => {
    //   window.localStorage.setItem('iut2jkqwpxww', JSON.stringify(questionsList));
    // }
  }, []);
  const toggleCheck = (id) => {
    const item = JSON.parse(window.localStorage.getItem("iut2jkqwpxww"));
    console.log("toggle check", id);
    const tempList = [...viewAbleQuestions];
    const checkedRowIndex = tempList.findIndex((item) => item.id === id);
    const value = tempList[checkedRowIndex].checked;
    console.log("toggle check value", value);
    tempList[checkedRowIndex] = {
      ...tempList[checkedRowIndex],
      checked: !value,
    };
    item[id] = { checked: !value };
    const tempCount = !value ? solveCount + 1 : solveCount - 1;
    if (hideSolved) {
      const newArr = tempList.filter(
        (item, index) => index !== checkedRowIndex
      );
      setViewAbleQuestions(newArr);
    } else {
      setViewAbleQuestions(tempList);
    }
    setQuestionsList(tempList);
    setSolveCount(tempCount);

    window.localStorage.setItem("iut2jkqwpxww", JSON.stringify(item));
  };
  const toggleShow = (e) => {
    setShowQuestions(!showQuestions);
  };
  const toggleAnswer = () => {
    console.log("clicked");
    if (hideSolved) {
      window.localStorage.setItem("hide" + meta.category, "false");
      setViewAbleQuestions(questionsList);
      setHideSolved(false);
    } else {
      window.localStorage.setItem("hide" + meta.category, "true");
      let tempQues = questionsList.filter((q) => !q.checked);
      setHideSolved(true);
      setViewAbleQuestions(tempQues);
    }
  };
  return (
    <StyledProject>
      <div className="project-inner" onClick={toggleShow}>
        <header style={{ width: "33%" }}>
          <div className="project-top">
            {/* <div className="folder">
                  icon
                </div> */}
            <div className="project-links">
              {1 > 2 && (
                <a
                  href={""}
                  aria-label="GitHub Link"
                  target="_blank"
                  rel="noreferrer"
                ></a>
              )}
              {2 > 3 && (
                <a
                  href={""}
                  aria-label="External Link"
                  className="external"
                  target="_blank"
                  rel="noreferrer"
                ></a>
              )}
            </div>
          </div>

          <h3 className="project-title">{meta.category}</h3>
          {/* <div className="project-description"> Backtracking </div> */}
        </header>
        <div
          className="hide-on-mobile problems-dist"
          style={{ width: "100px", height: "70px", width: "33%" }}
          data-for="mains"
          data-tip={`You've solved ${solveCount} problems out of ${questionsList.length}`}
          data-iscapture="true"
        >
          <PieChart
            lineWidth={20}
            rounded
            animate={true}
            data={[
              {
                title: `Easy-${questionInfo.Easy ? questionInfo.Easy : "0"}`,
                value: questionInfo.Easy ? questionInfo.Easy : 0,
                color: "var(--light-green)",
              },
              {
                title: `Medium-${
                  questionInfo.Medium ? questionInfo.Medium : "0"
                }`,
                value: questionInfo.Medium ? questionInfo.Medium : 0,
                color: "#f19e06",
              },
              {
                title: `Hard-${questionInfo.Hard ? questionInfo.Hard : "0"}`,
                value: questionInfo.Hard ? questionInfo.Hard : 0,
                color: "#f51212",
              },
            ]}
          />
          <h5 className="chart-helper">Problems</h5>
        </div>
        <div
          style={{
            width: "100px",
            height: "70px",
            alignSelf: "flex-start",
            width: "33%",
          }}
          className="problems-solved"
        >
          <CircularProgressbar
            value={`${Math.round((solveCount * 100) / questionsList.length)}`}
            text={`${solveCount} / ${questionsList.length}`}
            styles={buildStyles({
              textColor: "var(--lightest-slate)",
              pathColor: "var(--light-green)",
            })}
          />
          <h5 data-tip={`you have solved ${solveCount} problems`} className="chart-helper">
            Solved
          </h5>
          <ReactTooltip place="left" type="dark" effect="float" />
        </div>
      </div>
      <div
        className="question-section"
        style={{ display: showQuestions ? "" : "none" }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
          }}
        >
          {/* <button type="checkbox" className="resume-button" onClick={toggleAnswer}> Hide Answered </button> */}
          <label className="card-label">
            <span style={{fontSize:'14px',fontWeight:'500',marginRight:'5px',marginTop:'5px',color:'var(--lightest-slate)'}}>hide answered</span>
            <Switch
              onChange={toggleAnswer}
              checked={hideSolved}
              onColor="#86d3ff"
              onHandleColor="#2693e6"
              handleDiameter={20}
              uncheckedIcon={false}
              checkedIcon={false}
              boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
              activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
              height={14}
              width={28}
              className="react-switch"
              id="material-switch"
            />
          </label>
        </div>
        {showQuestions && (
          <Table questions={viewAbleQuestions} toggleCheck={toggleCheck} />
        )}
      </div>
    </StyledProject>
  );
};

export default Card;
