import React from "react";
import Card from "../Components/Card/Card";
// import Table from "./Components/Table/Table";
import styled from "styled-components";
import questions from "../Assets/data/data";
const StyledDashboard = styled.div`
  margin-top: 110px;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
`;
const Dashboard = (props) => {
    const groupedQuestions = groupBy(questions, 'category');
    console.log(groupedQuestions);
    const item = Object.keys(groupedQuestions).map((cat) => {
      const metaInfo = {category:cat,patterns:groupedQuestions[cat][0].pattern}
      return <Card key={cat} meta={metaInfo}  questionList={groupedQuestions[cat]} />;
  });
  return <StyledDashboard>{item}</StyledDashboard>;
};
export default Dashboard;


export const groupBy = (modelConnectionNodes, key) => {
    const groupedEntities = {};
    modelConnectionNodes.forEach((item) => {
      const keyId = item[key];
      groupedEntities[keyId] = groupedEntities[keyId]
        ? [...groupedEntities[keyId], item]
        : [item];
    });
    return groupedEntities;
  };