export const isMobile = () => window.matchMedia("(max-width: 700px)").matches;

export const tourSteps = [
  {
    selector: ".logo",
    content: () => (
      <div>
        <h2>Welcome!</h2>
        <div color="#e5e5e5">
          Let's take a complete tour of the application.
        </div>
        <div color="#373737" size=".7em" style={{ marginTop: ".7em" }}>
          click next to start
        </div>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".project-inner",
    content: () => (
      <div>
        <h2> Cards </h2>
        <p>
          {" "}
          Each Card consist of questions on the topic. You need to click on the
          card to open the questions.{" "}
        </p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".project-title",
    content: () => (
      <div>
        <h2> Category </h2>
        <p>
          This is the topic name. Each question in this card will be related to
          this topic.{" "}
        </p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".problems-dist",
    content: () => (
      <div>
        <h2> Problems </h2>
        <p>
          This Chart shows the problem distribution based on the difficulty.
        </p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: false,
  },
  {
    selector: ".problems-solved",
    content: () => (
      <div>
        <h2> Solved </h2>
        <p>This Chart shows the problems you have solved.</p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".project-inner",
    content: () => (
      <div>
        <h2> Open the questions </h2>
        <p>  Click on the highlighed Card above!</p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".title",
    content: () => (
      <div>
        <h2> Problem name </h2>
        <p>
          This is the problem name. Clicking on this column will open this
          problem in a new tab.
        </p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".overline",
    content: () => (
      <div>
        <h2> Check </h2>
        <p>To track your progress check the questions you've completed.</p>
        <p>Remember, time is the key.</p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
  {
    selector: ".links",
    content: () => (
      <div>
        <h2> Solution </h2>
        <p>
          click on this link to open the leetcode discussion tab for this
          problem.
        </p>
        <p>Remember, take help only when you don't get the problem at all.</p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: false,
  },
  {
    selector: ".pattern",
    content: () => (
      <div>
        <h2> Asked by </h2>
        <p>
          This column tells if this questions was asked by companies in real
          interviews.
        </p>
        <p>Based on Leetcode.</p>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: false,
  },
  {
    selector: ".card-label",
    content: () => (
      <div>
        <h2> Hide </h2>
        <p>Turn on this switch to hide the questions you've already solved.</p>
        <div>
          <div style={{ marginTop: "10px" }}>
            <h5> Finished </h5>
            <p> Click on Close to dismiss the tour.</p>
            <p>
              <input type="checkbox" defaultChecked />
              <span style={{ marginLeft: "5px" }}> Don't show again! </span>
            </p>
          </div>
        </div>
      </div>
    ),
    style: {
      backgroundColor: "var(--navy)",
      color: "var(--slate)",
    },
    showOnMobile: true,
  },
];
