import React, { useRef, useEffect } from "react";
// import { Helmet } from 'react-helmet';
import styled from "styled-components";
import { srConfig } from "../../config";
import sr from "../../util/sr";
// import { Icon } from '@components/icons';
// import { usePrefersReducedMotion } from '@hooks';
import { Icon } from "../icons";
import Checkbox from "../Checkbox";
import ReactTooltip from "react-tooltip";

const StyledTableContainer = styled.div`
  margin: 50px -20px;

  @media (max-width: 768px) {
    margin: 50px -10px;
  }

  .company-icon {
    /* height:20px !important; */
    /* width: 20px !important; */
    padding: 5px;
  }
  table {
    width: 100%;
    border-collapse: collapse;

    .hide-on-mobile {
      @media (max-width: 768px) {
        display: none;
      }
    }

    tbody tr {
      &:hover,
      &:focus {
        background-color: var(--light-navy);
      }
    }

    th,
    td {
      padding: 10px;
      text-align: left;

      &:first-child {
        padding-left: 20px;

        @media (max-width: 768px) {
          padding-left: 10px;
        }
      }
      &:last-child {
        padding-right: 20px;

        @media (max-width: 768px) {
          padding-right: 10px;
        }
      }

      svg {
        width: 20px;
        height: 20px;
      }
    }

    tr {
      cursor: default;

      td:first-child {
        border-top-left-radius: var(--border-radius);
        border-bottom-left-radius: var(--border-radius);
      }
      td:last-child {
        border-top-right-radius: var(--border-radius);
        border-bottom-right-radius: var(--border-radius);
      }
    }

    td {
      &.checkbox {
        padding-right: 20px;

        @media (max-width: 768px) {
          padding-right: 10px;
          font-size: var(--fz-sm);
        }
      }

      &.title {
        padding-top: 15px;
        padding-right: 20px;
        color: var(--lightest-slate);
        font-size: var(--fz-xl);
        font-weight: 600;
        line-height: 1.25;
        @media (max-width: 768px) {
            font-size: var(--fz-xs)
        }
      }

      &.difficulty {
        font-size: var(--fz-lg);
        white-space: nowrap;
        @media (max-width: 768px) {
            font-size: var(--fz-xs)
        }
      }

      &.pattern {
        font-size: var(--fz-xxs);
        font-family: var(--font-mono);
        line-height: 1.5;
        .separator {
          margin: 0 5px;
        }
        span {
          display: inline-block;
        }
      }

      &.links {
        min-width: 100px;

        div {
          display: flex;
          align-items: center;

          a {
            ${({ theme }) => theme.mixins.flexCenter};
            flex-shrink: 0;
          }

          a + a {
            margin-left: 10px;
          }
        }
      }
    }
  }
`;

const Table = (props) => {
  const { questions:projects } = props;
  const revealTitle = useRef(null);
  const revealTable = useRef(null);
  const revealProjects = useRef([]);
  const prefersReducedMotion = false; //usePrefersReducedMotion();
  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealTitle.current, srConfig());
    sr.reveal(revealTable.current, srConfig(200, 0));
    revealProjects.current.forEach((ref, i) =>
      sr.reveal(ref, srConfig(i * 10))
    );
  }, []);

  return (
    <StyledTableContainer ref={revealTable}>
      <table>
        <thead>
          <tr>
            <th></th>
            <th>Question</th>
            <th>Difficulty</th>
            {/* <th className="hide-on-mobile">Pattern</th> */}
            <th className="hide-on-mobile">Solution</th>
            <th className="hide-on-mobile">Asked In</th>
          </tr>
        </thead>
        <tbody>
          {projects.length > 0 &&
            projects.map((node, i) => {
              const {
                url,
                premium,
                id,
                name,
                companies,
                difficulty,
                checked,
                pattern,
              } = node;
              return (
                <tr key={i} ref={(el) => (revealProjects.current[i] = el)}>
                  <td className="overline checkbox">
                    <Checkbox
                      checked={checked}
                      onCheck={(e) => props.toggleCheck(id)}
                    />
                  </td>

                  <td className="title">
                    <a href={url}>
                      {name} <Icon name="LeetCode" />{" "}
                    </a>
                  </td>
                  <td className="difficulty">
                    {difficulty ? <span>{difficulty}</span> : <span></span>}
                  </td>
                  {/* <td className="pattern hide-on-mobile">
                    {pattern.length > 0 &&
                      pattern.map((item, i) => (
                        <span key={i}>
                          { 1>2? item: `${new Array(item.length).fill('*').join('')}` }
                          {""}
                          {i !== pattern.length - 1 && (
                            <span className="separator">&middot;</span>
                          )}
                        </span>
                      ))}
                  </td> */}
                  <td className="links hide-on-mobile">
                    <div>
                      {url && (
                        <a
                          href={
                            url + "discuss/?currentPage=1&orderBy=most_votes"
                          }
                          // aria-label="External Link"
                          className="company-icon"
                          data-for="main"
                          data-tip="LeetCode discussions"
                          data-iscapture="true"
                        >
                          <Icon name="External"  />
                        </a>
                      )}
                    </div>
                  </td>

                  <td className="pattern hide-on-mobile">
                    {companies.length > 0 &&
                      companies.map((item, i) => (
                        <span
                          key={i}
                          className="company-icon"
                          data-for="main"
                          data-tip={item}
                          data-iscapture="true"
                        >
                          <Icon key={i} name={`${item}`} />
                        </span>
                      ))}
                    <ReactTooltip
                      id="main"
                      place={"top"}
                      type={"dark"}
                      effect={"float"}
                      multiline={true}
                    />
                  </td>
                </tr>
              );
            })}
        </tbody>
      </table>
    </StyledTableContainer>
  );
};
// ArchivePage.propTypes = {
//   location: PropTypes.object.isRequired,
//   data: PropTypes.object.isRequired,
// };

export default Table;
