import React from "react";
import styled from "styled-components";
import { tips } from "../Assets/data/tips";
import { Icon } from "../Components/icons";
const StyledSection = styled.section`


.tip-lists{
 list-style-type:none;
}


/* .tip::before{
    content: '\2022';
    color: var(--green);
} */

`;

const StyledDiv = styled.div`
  margin-top: 110px;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  .tip-list {
    list-style-type: none;
    margin: 10px;
    padding: 0;
  }

  .tip {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 90px;
    margin: 20px 0px;
    border-radius: 5px;
    padding: 10px;
    width: 100%;
    background: var(--light-navy);
  }

  .technique {
    width: 20%;
  }

  .pattern {
    width: 60%;
  }
  .example {
    width: 20%;
    justify-self: flex-end;
    a {
      ${({ theme }) => theme.mixins.flexCenter};
      flex-shrink: 0;
    }

    a + a {
      margin-left: 10px;
    }
  }
  .p-head {
    margin: 0 !important;
    padding: 0 !important;
  }
  @media (max-width: 768px) {
    .tip {
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
      flex-grow: 1 1 auto;
      text-align: center;
    }
    .pattern {
      width: auto;
      margin:10px;
    }
    .example {
      width: auto;
      margin:10px;

    }
    .technique {
      width: auto;
      margin:10px;

    }
  }
`;

const Tips = () => {
  return (
    <StyledDiv>
      <ul className="tip-list">
        {tips.map((tip, index) => {
          return (
            <li key={index} className="tip">
              <div className="pattern">
                <h5 className="p-head">{tip.pattern} </h5>
              </div>
              <div className="technique">
                <h4 className="p-head">{tip.techniques.join(" . ")}</h4>
              </div>
              <div className="example">
                <a href={tip.examples[0]} style={{ height: "20px" }}>
                  <Icon name="External" />
                </a>
              </div>
            </li>
          );
        })}
      </ul>
    </StyledDiv>
  );
};

export default Tips;
